.lds-dual-ring:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid rgb(0, 255, 136);
    border-color: rgb(0, 255, 136) transparent rgb(0, 255, 136) transparent;
